package com.minutiae.game

import androidx.compose.runtime.*
import org.jetbrains.compose.web.css.Style
import org.jetbrains.compose.web.dom.Div
import org.jetbrains.compose.web.dom.P
import org.jetbrains.compose.web.dom.Text
import kotlin.random.Random

const val TESTING = false

var gameInstance: GameInstance? by mutableStateOf(null)

fun clearGame() {
    gameInstance = null
}

fun newGame() {
    gameInstance = GameInstance()
}

@Composable
fun Minutiae() {
    Style(Styles)
    Div({ classes(Styles.pageContainer) }) {
        gameInstance?.render() ?: MainMenu()
    }
}

@Composable
fun MainMenu() {
    Div({ classes(Styles.mainMenu) }) {
        P { Text("Welcome to the demo!") }
        P { Text("During the game, you will be given random trivia questions. Each round requires more points than the last, so you'll have to buy modifiers in the shop between rounds to improve your chances of continuing. Starting out, you'll have up to 5 questions to score the required points. You will also be given 2 skips, if you need them.") }
        P { Text("For each question, you will be given two options to pick from. The first will always be an Easy question, while the second option can be any difficulty (Easy, Medium, or Hard). Clicking the Reveal button below one of the questions will activate it, leaving the other one locked. Once activated, you will have a limited time to answer the question, so it's recommended to read through the questions before clicking Reveal.") }
        P { Text("There are 3 values to be aware of when questions are scored. The blue value is the amount of time taken to answer the question. This has a maximum of 5 (before modifiers); questions that have longer answers will allow more time to answer, so you might have to keep an eye on the blue value because once it starts going down, you only have a few seconds left to answer.") }
        P { Text("The red value is the number of guesses remaining. This starts at 3, and will go down each time you guess an answer incorrectly. If you guess incorrectly 3 times, you will get no points and be moved to the next question.") }
        P { Text("The green value is the difficulty of the question. This value will be 1, 2, or 3 for Easy, Medium, or Hard questions, respectively. Once a correct answer is given, the three numbers are multiplied and added to your total score for the round.") }
        P { Text("At the end of the round, you will be rewarded with some money depending on how many questions and skips you have left. You'll be given $1 for each unused question, and $1 for every 2 skips (rounded down).") }
        P { Text("After winning a round, you will be taken to the shop, where you can buy modifiers. Modifiers can give more points, make certain categories of questions more common, or anything else, really. They cost money, so you'll need to have enough to be able to afford them, and they are random every time you visit the shop.") }
        P { Text("As a reminder, this is just a proof of concept meant to show what the game would be about if it is made into a full game. It isn't graphically pleasing, and there is no end goal yet. It is just meant to demonstrate the core concepts of the game. To get started, click the New Game button below.") }
        StandardButton({
            onClick { newGame() }
        }) {
            Text("New Game")
        }

        StandardButton({
            onClick { cycleTheme() }
        }) {
            Text("Change Theme")
        }
    }
}
