package com.minutiae.game

import androidx.compose.runtime.*
import model.Question
import com.minutiae.game.event.EventBus
import com.minutiae.game.event.types.ScoreQuestionEvent
import org.jetbrains.compose.web.dom.Div
import org.jetbrains.compose.web.dom.Text
import kotlin.random.Random
import kotlin.collections.sumOf
import kotlin.math.max
import kotlin.math.min

class QuestionInstance(
    val roundInstance: RoundInstance,
    val question: Question
) {
    private val answers = question.getAnswers(Random).map(::AnswerInstance)

    var started by mutableStateOf(false); private set

    private var timeRemaining by mutableStateOf(max(answers.sumOf { it.text.split(" ").size } / 4, 5) + 2)
    var guessesRemaining by mutableStateOf(question.incorrectAnswers.size); private set
    val difficultyMultiplier = question.difficulty.multiplier

    private val score: Int get() {
        return EventBus.post(
            ScoreQuestionEvent(
                this,
                maxTime,
                guessesRemaining,
                difficultyMultiplier
            )
        ).score
    }

    fun onGuess(correct: Boolean, instance: GameInstance) {
        if (correct) {
            roundInstance.onCorrectAnswer(score, instance)
        } else {
            guessesRemaining--
            if (guessesRemaining == 0) roundInstance.onWrongAnswer(instance)
        }
    }

    val maxTime get() = min(timeRemaining, 5)

    @Composable
    fun render(instance: GameInstance, showButton: Boolean) {
        if (started) {
//            LaunchedEffect(Unit) {
//                while (timeRemaining > 0) {
//                    delay(1.seconds)
//                    timeRemaining--
//                }
//                roundInstance.onWrongAnswer(instance)
//            }
        }

        if (showButton) {
            RenderHidden()
        } else {
            RenderSelected(instance)
        }
    }

    @Composable
    private fun RenderSelected(instance: GameInstance) {
        Div({ classes(Styles.question, Styles.questionRevealed) }) {
            Div({ classes(Styles.answersContainer) }) {
                answers.forEach {
                    it.render(this@QuestionInstance, instance)
                }
            }
            QuestionBlock()
        }
    }

    @Composable
    private fun RenderHidden() {
        Div({ classes(Styles.question, Styles.questionHidden) }) {
            QuestionBlock()

            StandardButton({
                onClick {
                    started = true
                    roundInstance.questionsRemaining--
                }
            }) {
                Text("Reveal!")
            }
        }
    }

    @Composable
    private fun QuestionBlock() {
        Div({ classes(Styles.questionCategoryAndText) }) {
            Div({ classes(Styles.secondaryComponent) }) {
                question.run {
                    SimpleText("${category.displayName} [${difficulty.displayName}]")
                }
            }

            Div({ classes(Styles.secondaryComponent) }) {
                SimpleText(question.text)
            }
        }
    }
}


