package com.minutiae.game

import androidx.compose.runtime.*
import model.Answer
import org.jetbrains.compose.web.css.textDecoration
import org.jetbrains.compose.web.dom.Div
import org.jetbrains.compose.web.dom.Text

class AnswerInstance(private val answer: Answer) {
    private var picked by mutableStateOf(false)

    val text get() = answer.text

    @Composable
    fun render(questionInstance: QuestionInstance, instance: GameInstance) {
        StyledText(answer.text) {
            classes(Styles.answer)

            if (picked) {
                classes(Styles.wrongGuess)
            } else {
                onClick {
                    picked = true
                    questionInstance.onGuess(answer.isCorrect, instance)
                }
            }
        }
    }
}
