package com.minutiae.game.modifier

import androidx.compose.runtime.*
import com.minutiae.game.*
import com.minutiae.game.event.types.Event
import org.jetbrains.compose.web.css.*
import org.jetbrains.compose.web.dom.*

class GameModifierInstance(
    val modifier: GameModifier,
    var value: Int,
    dataState: MutableState<Map<String, Any?>>
) {
    private var data by dataState

    fun <T : Event> onEvent(event: T) {
        modifier.onEvent(this, event)
    }

    operator fun <T> set(key: String, value: T) {
        data = data.toMutableMap().also { it[key] = value }
    }

    operator fun <T> get(key: String) = data[key] as? T

    @Composable
    fun renderInShop(instance: GameInstance, shopInstance: ShopInstance) {
        Render {
            ModifierButton("Buy") {
                if (instance.buyModifier(this@GameModifierInstance)) {
                    shopInstance.modifiers = shopInstance.modifiers.filter { it !== this@GameModifierInstance }
                }
            }
        }
    }

    @Composable
    private fun Render(buttons: @Composable () -> Unit) {
        var isDescriptive by mutableStateOf(false)

        Div({
            classes(Styles.modifier)
            onClick { isDescriptive = !isDescriptive }
        }) {
            StyledInlineText(modifier.getName(data)) { classes(Styles.modifierName) }
            Span({
                classes(Styles.simpleText)
                style {
                    display(DisplayStyle.Inline)
                }
            }) {
                Text("Value: ")
                Span({ style { color(Color.darkgreen) } }) { Text("$$value") }
            }

            if (isDescriptive) modifier.describe(data)
            else SimpleText("Tap for Info")

            Div({ classes(Styles.modifierButtons) }) { buttons() }
        }
    }

    @Composable
    fun renderInContainer(instance: GameInstance) {
        Render {
            ModifierButton("") { instance.moveModifier(this@GameModifierInstance, -1) }
            ModifierButton("Sell") { instance.sellModifier(this@GameModifierInstance) }
            ModifierButton("") { instance.moveModifier(this@GameModifierInstance, 1) }
        }
    }

    @Composable
    private fun ModifierButton(
        text: String,
        onClick: () -> Unit
    ) = StandardButton({
        classes(Styles.modifierButton)
        onClick { onClick() }
    }) {
        Text(text)
    }
}
