package com.minutiae.game

import androidx.compose.runtime.*
import com.minutiae.game.event.EventBus
import com.minutiae.game.event.types.GameStartEvent
import com.minutiae.game.event.types.RequiredScoreEvent
import com.minutiae.game.event.types.RunInfoEvent
import com.minutiae.game.modifier.GameModifierInstance
import com.minutiae.game.modifier.GameModifiers
import com.minutiae.weight.getRandomItems
import com.minutiae.weight.takeRandomItems
import org.jetbrains.compose.web.dom.Div
import kotlin.math.pow
import kotlin.random.Random

class GameInstance {
    var modifiers by mutableStateOf(
        GameModifiers.toMutableList().takeRandomItems(Random, 5) { it.weight }.map {
            it.getInstance()
        }
    ); private set

    var roundsPlayed by mutableStateOf(0)
    var money by mutableStateOf(0)

    val requiredScore get() = EventBus.post(RequiredScoreEvent((1.7.pow(roundsPlayed) * 20).toInt())).requiredScore
    val runInfo get() = EventBus.post(RunInfoEvent(5, 2))

    var currentScreen by mutableStateOf<PlayScreen>(newRound())

    init {
        EventBus.post(GameStartEvent(this))
    }

    fun moveModifier(modifier: GameModifierInstance, direction: Int) {
        if (modifiers.isEmpty()) return

        val index = modifiers.indexOf(modifier)
        val size = modifiers.size
        val newIndex = (index + direction + size) % size
        val itemAt = modifiers[newIndex]

        modifiers = modifiers.toMutableList().also { list ->
            list[index] = itemAt
            list[newIndex] = modifier
        }
    }

    fun buyModifier(modifier: GameModifierInstance): Boolean {
        if (money < modifier.value) return false
        if (modifiers.size == 5) return false

        modifiers = (modifiers + modifier)
        money -= modifier.value
        modifier.value -= 1
        return true
    }

    fun removeModifier(modifier: GameModifierInstance) {
        modifiers = modifiers.filter { it !== modifier }
    }

    fun sellModifier(modifier: GameModifierInstance) {
        val value = modifier.value
        money += value
        removeModifier(modifier)
    }

    fun startRound() {
        currentScreen = newRound()
    }

    fun openShop() {
        currentScreen = ShopInstance(Random)
    }

    private fun newRound(): RoundInstance {
        val runInfo = runInfo
        val requiredScore = requiredScore

        return RoundInstance(
            requiredScore,
            runInfo.questions,
            runInfo.skips,
            Random
        )
    }

    @Composable
    fun render() {
        Div({ classes(Styles.wholeScreen) }) {
            Div({ classes(Styles.sidebar) }) {
                if (!currentScreen.renderSideBar(this@GameInstance)) {
                    val runInfo = runInfo
                    Sidebar(
                        runInfo.questions,
                        runInfo.skips,
                        roundsPlayed,
                        money
                    )
                }
            }

            Div({ classes(Styles.playScreen) }) {
                Div({ classes(Styles.modifierContainer) }) {
                    modifiers.forEach { modifier ->
                        modifier.renderInContainer(this@GameInstance)
                    }
                }
                currentScreen.render(this@GameInstance)
            }
        }
    }
}