package com.minutiae.game

import androidx.compose.runtime.Composable
import org.jetbrains.compose.web.css.CSSColorValue
import org.jetbrains.compose.web.dom.*
import org.w3c.dom.HTMLButtonElement
import org.w3c.dom.HTMLDivElement
import org.w3c.dom.HTMLSpanElement

fun <R> assuming(condition: Boolean, block: () -> R) = if (condition) block() else null

inline fun <T> Iterable<T>.sumOf(selector: (T) -> Float): Float {
    var sum = 0f
    forEach { sum += selector(it) }
    return sum
}

data class HEX(private val code: String) : CSSColorValue {
    override fun toString(): String = "#$code"
}

@Composable
fun StyledText(value: String, attrs: AttrBuilderContext<HTMLDivElement>? = null) = Div(attrs) { Text(value) }

@Composable
fun StyledInlineText(value: String, attrs: AttrBuilderContext<HTMLSpanElement>? = null) = Span(attrs) { Text(value) }

@Composable
fun SimpleText(value: String) = Span({ classes(Styles.simpleText) }) { Text(value) }

@Composable
fun StandardButton(
    attrs: AttrBuilderContext<HTMLButtonElement>? = null,
    content: ContentBuilder<HTMLButtonElement>? = null
) = Button(
    {
        classes(Styles.standardButton)
        attrs?.invoke(this)
    },
    content
)
