package com.minutiae.game

import com.minutiae.api.TheTriviaAPI
import model.*

object QuestionPool {
    private val pools = mutableMapOf<Pair<Difficulty, Category>, MutableList<Question>>()

    private suspend fun checkQuestions(difficulty: Difficulty, category: Category) {
        val questionPool = pools.getOrPut(difficulty to category, ::mutableListOf)
        if (questionPool.isNotEmpty()) return

        questionPool += if (TESTING) {
            List(10) {
                Question(
                    category,
                    "ID",
                    emptyList(),
                    difficulty,
                    emptyList(),
                    false,
                    QuestionText("Question?"),
                    "Correct Answer",
                    listOf("Wrong", "Nope", "Sorry"),
                    "text"
                )
            }
        } else {
            TheTriviaAPI.getQuestions(
                QuestionRequest(
                    limit = 10,
                    difficulties = listOf(difficulty),
                    categories = listOf(category)
                )
            )
        }
    }

    suspend fun getQuestion(difficulty: Difficulty, category: Category): Question {
        checkQuestions(difficulty, category)
        return pools[difficulty to category]!!.removeFirst()
    }
}