package com.minutiae.game.event

import com.minutiae.game.event.types.Event
import kotlin.reflect.KClass

fun <K : Any, T> Map<KClass<out K>, T>.forEachMatching(k: K, block: (T) -> Unit) {
    forEach { (kClass, target) ->
        if (kClass.isInstance(k)) block(target)
    }
}

typealias EventHandler<T> = (T) -> Unit

object EventBus {
    private val listeners = mutableMapOf<KClass<out Event>, MutableList<EventHandler<Event>>>()

    operator fun <T : Event> set(kClass: KClass<out T>, block: EventHandler<T>) {
        listeners.getOrPut(kClass, ::mutableListOf) += block as EventHandler<Event>
    }

    fun <T : Event> post(event: T): T {
        listeners.forEachMatching(event) { handlers ->
            handlers.forEach { it(event) }
        }
        return event
    }
}

